<template>
  <div id='eventManage'>
     <dialog-wropper top="200px" :di="isDShowParam" class="work-conn-dialog" @saveDialog="saveDialogParam" @closeDialog="closeDialogParam">
        <div slot="container">
            1233
        </div>
    </dialog-wropper>
    <div class='title'>
      <div>事件管理</div>
    </div>
    <div class='format-group'>
      <span class="span-tro">是否重大事件:</span>
      <el-select
        v-model="searchForm.importantEvent"
        placeholder="全部"
        class="sel"
        clearable
      >
        <el-option
          :key="3"
          label="全部"
          value=''
        >
        </el-option>
        <el-option
          :key="0"
          label="是"
          value='1'
        >
        </el-option>
        <el-option
          :key="1"
          label="否"
          value='0'
        >
        </el-option>
      </el-select>
    
      <span class="span-tro">是否告警事件:</span>
      <el-select
        v-model="searchForm.alarmEvent"
        placeholder="请选择"
        class="sel"
        clearable
      >
        <el-option
          :key="0"
          label="全部"
          value=''
        >
        </el-option>
        <el-option
          :key="1"
          label="是"
          value='1'
        >
        </el-option>
        <el-option
          :key="2"
          label="否"
          value='0'
        >

        </el-option>
      </el-select>
       <span class="span-tro">事件状态:</span>
      <el-select
        v-model="searchForm.eventStatus"
        placeholder="请选择"
        class="sel"
        clearable
      >
        <el-option
          :key="0"
          label="全部"
          value=''
        >
        </el-option>
        <el-option
          :key="1"
          label="开启"
          value='1'
        >
        </el-option>
        <el-option
          :key="2"
          label="关闭"
          value='0'
        >

        </el-option>
      </el-select>
      <el-input
        placeholder="请输入事件名"
        v-model.trim="searchForm.eventName"
        clearable
        style="width: 140px"
      >
      </el-input>
      <el-button type="primary" @click="getTableList" size="small">查询</el-button>
      <!-- {{searchForm}} -->
      <div style="display:inline-block;float:right">
        <el-button type="primary" @click="addeventManage" size="small">新增自定义事件</el-button>
        <el-button type="primary" size="small" @click="moreDel" :disabled="multipleSelection.length === 0">批量删除</el-button>
      </div>

    </div>
    <!-- <div>{{eventData}}</div> -->
    <div class='eventTable'>
      <el-table
        ref="multipleTable"
        :data="eventData"
        style="width: 100%"
        @selection-change="handleSelectionChange"
      >
        <el-table-column
          type="selection"
          width="55">
        </el-table-column>
        <el-table-column
          type="index"
          label="序号"
          width="50"
        >
        </el-table-column>
        <el-table-column
          prop="eventName"
          label="事件名称"
        >
        </el-table-column>
        <el-table-column
          prop="description"
          label="事件描述"
        >
        </el-table-column>
        <el-table-column label="是否列为告警事件">
          <template slot-scope="scope">
            <el-switch
              @change="changeRow(scope.row)"
              v-model="eventData[scope.$index].importantEvent === 1 ? eventData[scope.$index].alarmEvent = 1 : eventData[scope.$index].alarmEvent"
              :active-value="1"
              :inactive-value="0"
              :disabled="eventData[scope.$index].importantEvent === 1"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="是否列为重大事件">
          <template slot-scope="scope">
            <el-switch
              @change="changeRow(scope.row)"
              v-model="eventData[scope.$index].importantEvent"
              :active-value="1"
              :inactive-value="0"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="事件状态">
         <template slot-scope="scope">
            <el-switch
              @change="changeRow(scope.row)"
              v-model="eventData[scope.$index].eventStatus"
              :active-value="1"
              :inactive-value="0"
              active-color="#13ce66"
              inactive-color="#ff4949"
            >
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button
              @click="editRow(scope.row)"
              size="mini"
              type="text"
            >编辑</el-button>
            <!-- @click="handleEdit(scope.$index, scope.row)" -->

            <el-button
              @click="delRow(scope.row, 0)"
              size="mini"
              type="text"
              :disabled="scope.row.defaultStatus == 1"
            >删除</el-button>
            <!-- @click="handleDelete(scope.$index, scope.row)" -->
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import APICONFIG from "../../../../../../config/api.js";
import dialogWropper from "./dialogWropper"

export default {
  components: {
    dialogWropper
  },
  data() {
    return {
      netApi: '',
      isDShowParam: {
        Visible: false,
        Title: '参数设置',
        Width: '500px'
      },
      eventData: [],
      name: "",
      status: "",
      isImportant: "",
      isAlert: "",
      searchForm: {
        alarmEvent: '',
        eventName: '',
        importantEvent: '',
        eventStatus: '',
        order: 'desc',
        sort: '',
        pageNo: 1,
        pageSize: 10
      },
      removeIds: [],
      multipleSelection: []
    };
  },
  methods: {
    // filterDiviceId(dex) {
    //   return 111
    // },
    moreDel() {
      this.$confirm('此操作将永久删除该事件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = this.multipleSelection.map(item => { return { id: item.id } })
        this.$http.post(this.netApi.remove, params).then(res => {
          if(res.data.status === 200) {
            this.$message.success('删除成功')
            this.getTableList()
          } else {
            this.$message.error('删除失败')
          }
        })
      }).catch(() => {
      });
    },
    // 选中框
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    addeventManage() {
      this.$router.push('addeventManage')
    },
    getTableList() {
      this.$http.get(this.netApi.find, {
        params: this.searchForm
      }).then(
        res => {
          if(res.body.status === 200) {
            this.eventData = res.body.data
          } else {
            this.$message.warning('查询失败')
          }
        }
      )
    },
    changeRow(row) {
      console.log(row)
      this.$http.post(this.netApi.modify, row).then(
        res => {
          if(res.body.status === 200) {
            this.$message.success('修改成功')
          } else {
            this.$message.warning('修改失败')
          }
        })
    },
    editRow(row) {
      // this.isDShowParam.Visible = true
      // this.isDShowParam.Title = '编辑事件规则'
      this.$router.push({ path: "addeventManage", query: { id: row.id } });
      // this.$router.push(`/monitoringSystem/monitoring_system/eventManage?id=${row.id}`)
      // this.$http.get(this.netApi.detail, {
      //   params: {
      //     id: row.id
      //   }
      // }).then(res => {

      // })
    },
    delRow(row, s) {
      console.log(row)
      if(!s) {
        //单个删除时
        this.removeIds.push({
          id: row.id
        })
      }
      this.$confirm('此操作将永久删除该事件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$http.post(this.netApi.remove, this.removeIds).then(
        res => {
          if(res.body.status === 200) {
            this.$message.success('删除成功')
            // 刷新列表
            this.getTableList()
          } else {
            this.$message.warning('修改失败')
          }
        })
      }).catch(() => {
      });
    },
    saveDialogParam() {
      this.isDShowParam.Visible = false
    },
    closeDialogParam() {
      this.isDShowParam.Visible = false
    }
  },
  watch: {
    // 'eventData': {
    //   handler(newVal) {
    //     console.log(newVal)
    //   },
    //   deep: true
    // }
  },

  created() {
    this.netApi = APICONFIG().get("event_manage");
    this.getTableList()
  },
};
</script>
<style lang="scss">
#eventManage {
  height: 800px;
  background: #fff;
  margin: 0 -0.96rem;
  .span-tro {
    margin-left: 10px;
  }
  .title {
    height: 40px;
    border-bottom: 1px solid;
    font-size: 0.5rem;
    margin: 0 0.6rem;
    padding: 0.3rem 0;
    font-weight: bold;
  }
  .format-group {
    margin: 0.3rem 0.6rem;
    .el-input__inner {
        height: 34px !important;
    }
    .sel {
      width: 100px !important;
    }
  }
  .eventTable {
    margin: 0.3rem 0.6rem;
  }
  .operation-box {
    height: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 0 0.3rem;
  }
  .table-box {
    .pagination {
      text-align: center;
    }
  }
  .el-input {
    width: auto;
  }
}
</style>




